<template>
	<view>
		<!-- #ifndef MP-WEIXIN -->
		<uni-status-bar></uni-status-bar>
		<view @tap="back" class="iconfont icon-guanbi flex align-center justify-center font-lg size-100">
		</view>
		<!-- #endif -->

		<template v-if="choick">
			<!-- 账号密码登录 -->
			<view>
				<view class="text-center" style="padding-top: 90rpx; padding-bottom: 90rpx;">
					<text class="h3 text-body">账号密码登录</text>
				</view>
				<view class="flex md-2 p-2">
					<input type="text" v-model="phone" placeholder="手机号" class="border-bottom p-2 flex-1">
				</view>
				<view class="flex md-2 p-2">
					<input type="password" v-model="password" placeholder="请输入密码" class="border-bottom p-2 flex-1">
					<view class="font-sm text-muted px-3 flex align-center">
						忘记密码?
					</view>
				</view>
				<view class="p-2">
					<button class="rounded-circle bg-pink text-white" @tap="login">登录</button>
				</view>
				<view class="text-center mt-3 text-primary font-sm">
					<text class="p-1" @tap="choick=!choick">验证码登录</text>
					<text class="text-body">|</text>
					<text class="p-1">登录遇到问题</text>
				</view>
				<view class="text-muted text-center mt-3">
					注册即代表同意
					<text class="text-primary">《xxx协议》</text>
				</view>
				<!-- 三种登录图标 -->
				<view class="iconfont_3 flex iconfont" style="width: 100%; justify-content: center;">
					<view class="icon green login-icon">
						<view class="iconfont icon-weixin"></view>
					</view>
					<view class="icon blue login-icon">
						<view class="iconfont icon-QQ"></view>
					</view>
					<view class="icon yellow login-icon">
						<view class="iconfont icon-xinlangweibo"></view>
					</view>
				</view>
			</view>
		</template>

		<template v-else>
			<view>
				<view class="text-center" style="padding-top: 90rpx; padding-bottom: 90rpx;">
					<text class="h3 text-body">手机验证码登录</text>
				</view>
				<view class="flex md-2 p-2">
					<view class="border-right font px-2 flex align-center">+86</view>
					<input type="text" v-model="phone" placeholder="手机号" class="border-bottom p-2 flex-1">
				</view>
				<view class="flex md-2 p-2">
					<input type="password" v-model="verifyCode" placeholder="请输入验证码" class="border-bottom p-2 flex-1">
					<view @tap="getCode" :class="limitTime > 0 ? 'bg-pink-disabled' : 'bg-pink'"
						class="font-sm px-3 flex align-center rounded text-white">
						获取验证码
					</view>
				</view>
				<view class="p-2">
					<button class="rounded-circle bg-pink text-white" disabled>登录</button>
				</view>
				<view class="text-center mt-3 text-primary font-sm">
					<text class="p-1" @tap="choick=!choick">账号密码登录</text>
					<text class="text-body">|</text>
					<text class="p-1">登录遇到问题</text>
				</view>
				<view class="text-muted text-center mt-3">
					注册即代表同意
					<text class="text-primary">《xxx协议》</text>
				</view>
			</view>
		</template>
	</view>
</template>

<script>
	import uniStatusBar from '@/components/uni/uni-status-bar/uni-status-bar.vue';
	export default {
		components: {
			uniStatusBar
		},
		data() {
			return {
				choick: true,
				phone: '',
				password: '',
				verifyCode: '',
				limitTime: 0,
			}
		},
		methods: {
			back() {
				uni.showToast({
					title: "返回",
					duration: 2000
				})
				// uni.navigateBack({
				// 	delta:1;
				// })
			},
			validate() {
				//手机号正则
				var mPattern = /^1[345789]\d{9}$/;
				if (!mPattern.test(this.phone)) {
					uni.showToast({
						title: '手机号格式不正确',
						icon: 'none'
					});
					return false;
				}
				// ...更多验证
				return true;
			},
			// login() {
			// 	// 账号密码登录
			// 	let data = {
			// 		username: '杨阳',
			// 		password: '123123'
			// 	}
			// 	const url = 'http://106.14.169.149:8071/login';
			// 	uni.request({
			// 		url: url,
			// 		method: 'POST',
			// 		data: data
			// 	}).then((res) => {
			// 		// console.log(res[1].data);
			// 		console.log(res[1].data.data.nickname);
			// 	})
			// },
			getCode() {
				// 防止重复点击获取验证码
				if (this.limitTime > 0) {
					return;
				}
				// 验证手机号(没通过)
				if (!this.validate()) {
					return;
				}
				this.limitTime = 10;
				let timer = setInterval(() => {
					if (this.limitTime >= 1) {
						this.limitTime--;
					} else {
						this.limitTime = 0;
						clearInterval(timer);
					}
				}, 1000)
			}
		}
	}
</script>

<style>

